* {
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}    

body {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}    

#container{
    display: flex;
    flex-direction: column;
    width: 270px;
    border-radius: 10px;
    padding: 30px;
    padding-top: 10px;
    background: black;
    color: white;
    height: 400px;
}    

#container div:first-of-type(1){
    height: 80%;
    background-color: red;
}

#container div:first-of-type(1){
    height: 20%;
}

.text_area{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 10px;
}    

input{
    margin: 10px 0px;
    border-radius: 5px;
    height: 70px;
    width: 100%;
    text-align: center;
}

button{
    margin: 15px 0;
    border: none;
    width: 100%;
    height: 50px;
    border-radius: 5px;
    color: white;
    background: rgb(118, 45, 212);
    cursor: pointer;
}

.conteudo_resultado{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}